import { createStore } from 'vuex'
//import api from "../assets/js/api.js";
const store = createStore({
    state: {
        auth:false,
        name:'',
        code:''
    },
    getters: {
      get_auth(state) {
        return state.auth;
      },
      get_name(state) {
        return state.name;
      },
      get_code(state) {
        return state.code;
      },
    },
    mutations: {
      change_auth(state, condition) {
        state.auth = condition;
      },
      change_name(state, condition) {
        state.name = condition;
      },
      change_code(state, condition) {
        state.code = condition;
      }
    },
    actions: {}
  });
