<template>
  <div v-if='auth' class="hello">
      <h1>Ваш личный кабинет для тестирования</h1>
    <h3>Добро пожаловать в ваш кабинет, здесь вы можете протестировать наше приложение
    </h3>
    <h3>Приносим свои извинения, но файл загрузить в данный момент нельзя
    </h3>
    <button disabled>
     Загрузить аудио или видео
    </button>
    <h3>Отправить текст для тестирования конспекта и тематики текста
    </h3>
    <textarea v-model="textEx" name="" id="" cols="30" rows="10"></textarea>
    <button @click="sendText(textEx)">
     {{buttonUpload}}
    </button>
    <p>Тематика текста:{{typeText}}</p>
    <p>Конспект текста:{{sumText}}</p>
    <h3>Хотите выйти?</h3>
    <button @click="logout()">
     Выйти
    </button>
  </div>
  <div v-else>
    <router-link to="/">
    <button>
     Войти в систему
    </button>
    </router-link>
  </div>
</template>

<script>
import router from '../router/index.js';
import api from "../assets/js/api.js";
export default {
  name: "home",
  data(){
    return{
      buttonUpload:'Загрузить текст',
      textEx:'',
      typeText:'',
      sumText:'',
      ex:true
    }
  },
  watch: {
    auth: function(val) {
    if(!val&&this.ex){
    router.push(`/`);
    this.$toast.error(`К сожалению, вы еще не вошли в аккаунт.`);
    }
    }
    },
  computed:{
    auth(){
      return this.$store.getters.get_auth;
    },
    name(){
      return this.$store.getters.get_name;
    },
    code(){
      return this.$store.getters.get_code;
    }
  },
  methods: {
    async sendText(text){
      this.buttonUpload='Загрузка...'
      let msg = await api.textGet({ text });
      if(msg){
        this.typeText=msg.desc;
        this.sumText=msg.consp;
      }
      this.buttonUpload='Загрузить текст'
      //typeText=
      //sumText=
    },
    logout() {
      this.ex=false;
      this.$store.commit('change_auth',false);
      api.logout();
      this.$toast.success(`Поздравляем,вы успешно покинули аккаунт`);
      router.push('/');
    },
  },
};
</script>

