<template>
  <div class="hello">
    <h1>Система работы с текстом</h1>
    <h3>Почта пользователя</h3>
    <div class="group">
      <input v-model="name" type="text" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Почта</label>
    </div>
    <h3>Пароль пользователя</h3>
    <div class="group">
      <input v-model="password" type="password" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Пароль</label>
    </div>
    <button @click="login(name, password)">
      Войти
    </button>
    <h3>Еще не зарегистрированы?</h3>
    <router-link to="/signup">
    <button class="btn_reg">
     Зарегистрироваться
    </button>
    </router-link>
  </div>
</template>

<script>
import router from '../router/index.js';
import api from "../assets/js/api.js";
export default {
  name: "login",
  data() {
    return {
      qr_code:'',
      name: "",
      password: "",
      url:'',
      ex:true
    };
  },
  computed:{
    auth(){
      return this.$store.getters.get_auth;
    },
    curname(){
      return this.$store.getters.get_name;
    },
    code(){
      return this.$store.getters.get_code;
    }
    },
    watch: {
    auth: function(val)  {
      if(val&&this.ex){
      this.$store.commit('change_name',this.curname)
      this.$store.commit('change_code',this.code)
      this.$toast.success(`Вы уже вошли в систему, прежде чем войти, выйдите из текущего пользователя`);
      router.push(`home`);
    }
    }
    },
  methods: {
    async login(name, password) {
      let msg= await api.login({ name, password });
      if(msg.displayName){
        this.$store.commit('change_auth',true)
        this.$toast.success(`Поздравляем, вы успешно вошли в систему под своим именем и паролем.`);
        router.push(`home`);
      }
      else
      {
       this.$toast.error(`К сожалению, пароль или имя пользователя не являются правильными, попробуйте снова.`);
      }
    },
  },
};
</script>

