<template>
  <div class="hello">
      <h1>Регистрация </h1>
    <h3>Логин пользователя</h3>
    <div class="group">
      <input v-model="name" type="text" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Логин</label>
    </div>
    <h3>Имя пользователя</h3>
    <div class="group">
      <input v-model="userN" type="text" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Имя</label>
    </div>
    <h3>Email пользователя</h3>
    <div class="group">
      <input v-model="email" type="text" inputmode="email" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Почта</label>
    </div>
    <h3>Пароль пользователя</h3>
    <div class="group">
      <input v-model="password" type="password" required />
      <span class="highlight"></span>
      <span class="bar"></span>
      <label>Пароль</label>
    </div>
    <button class="btn_reg" @click="signup(name, password,email,userN)">
      Отправить
    </button>
    <h3>Уже зарегистрированы?</h3>
    <router-link to="/">
    <button >
     Войти
    </button>
    </router-link>
  </div>
</template>

<script>
import router from '../router/index.js';
import api from "../assets/js/api.js";
export default {
  name: "signup",
  data() {
    return {
      name: "",
      password: "",
      email: "",
      userN: "",
    };
  },
  computed:{
    auth(){
      return this.$store.getters.get_auth;
    },
    curname(){
      return this.$store.getters.get_name;
    },
    code(){
      return this.$store.getters.get_code;
    }
    },
  watch: {
    auth: function (val) {
      if(val){
      this.$store.commit('change_name',this.curname)
      this.$store.commit('change_code',this.code)
      this.$toast.success(`Вы уже вошли в систему, прежде чем войти, выйдите из текущего пользователя`);
      router.push(`home`);
    }
    }
    },
  methods: {
    async signup(name, password,email,userN) {
      if(password.length<6){
         this.$toast.error(`Пароль должен быть не менее 6 символов`);
      }else{
      let msg = await api.signup({ name, password ,email,userN});
      if(msg.msg=='Успешно'){
       this.$toast.success(`Поздравляем, вы успешно зарегистрировались,осталось только войти в систему под своим именем и паролем.`);
       router.push('/');
      }else{
       this.$toast.error(`Попробуйте снова, кажется вы уже зарегистрированы или такой пользователь существует`);
      }
      }
    },
  },
};
</script>

