import axios from "axios";
class Api {
    constructor(options = {}) {
      this.client = options.client || axios.create();
      this.token = options.token;
      this.refreshToken = options.refreshToken;
      this.refreshRequest = null;
      this.client.interceptors.request.use(
        (config) => {
          if (!this.token) {
            return config;
          }

          const newConfig = {
            headers: {},
            ...config,
          };

          newConfig.headers.Authorization = `Bearer ${this.token}`;
          return newConfig;
        },
        (e) => Promise.reject(e)
      );

      this.client.interceptors.response.use(
        (r) => r,
        async (error) => {
          if (
            !this.refreshToken ||
            error.response.status !== 401 ||
            error.config.retry
          ) {
            throw error;
          }

          if (!this.refreshRequest) {
            this.refreshRequest = this.client.post("http://localhost:3000/auth/refresh", {
              refreshToken: this.refreshToken,
            });
          }
          const { data } = await this.refreshRequest;
          this.token = data.token;
          this.refreshToken = data.refreshToken;
          localStorage.setItem('token',data.token)
          localStorage.setItem('Rtoken',data.refreshToken)
          const newRequest = {
            ...error.config,
            retry: true,
          };

          return this.client(newRequest);
        }
      );
    }

    

    async login({ name, password }) {
      let email=name;
      try{
      const { data } = await this.client.post("https://926c-91-244-168-17.ngrok.io/api/User/login", {
        email,
        password,
      });
      localStorage.setItem('token',data.token)
      localStorage.setItem('Rtoken',data.refreshToken)
      this.token = data.token;
      this.refreshToken = data.refreshToken;
      return data;
    } catch(e){
      return {msg:'Error'}
    }
    }
    async textGet({ text}) {
      try{
      const { data } = await this.client.post(`https://926c-91-244-168-17.ngrok.io/api/Classify?text=${text}`);
      console.log(data)
      return data;
    } catch(e){
      return {msg:'Error'}
    }
    }

    logout() {
      this.token = null;
      this.refreshToken = null;
      localStorage.setItem('token','')
      localStorage.setItem('Rtoken','')
    }

   async signup({ name, password,email,userN }) {
      let login=name;
      try{
      const { data } = await this.client.post("https://926c-91-244-168-17.ngrok.io/api/User/registration", {
        displayName:login,
        password,
        email,
        userName:userN
      })
      console.log(data)
      localStorage.setItem('token',data.token)
      localStorage.setItem('Rtoken',data.refreshToken)
      this.token = data.token;
      this.refreshToken = data.refreshToken;
      return data;
    } catch(e){
      return {msg:'Сущесвует'};
    }
    }
}
let api= new Api();
export default api